<template>
  <div>
    <Header name="我的地址"></Header>
    <div class="ar-body">
      <div v-for="item in dataList" class="card-lists" :key="item.id">
        <dl>
          <dt>{{item.name}}<span>{{item.phoneNumber}}</span></dt>
          <dd>{{item.province}} {{item.city}} {{item.region}} {{item.town}}{{item.detailAddress}}</dd>
          <div>
            <div @click="updateDto(item)" style="display: inline;">
              <img :src="item.defaultStatus ? require('@/assets/image/radio.png') : require('@/assets/image/radio_gray.png')">
              默认地址
            </div>
            <!-- <div @click="addOrEdit(item)" style="display: inline;">
              <img style="float: right; width: 20px; margin-left: 10px;" :src="require('@/assets/image/edit.png')">
            </div>
            <div @click="addOrEdit(item)" style="display: inline;">
              <img style="float: right; width: 20px; margin-left: 10px;" :src="require('@/assets/image/edit.png')">
            </div> -->
            <van-icon @click="addOrEdit(item)" name="edit" style="margin-left: 15px;"/>
            <van-icon @click="deleteDto(item)" name="delete-o"/>
          </div>
        </dl>
      </div>
      <div class="btn" @click="addOrEdit">新增地址</div>
    </div>
  </div>
</template>

<script>

import * as api from '@/data/api/market/index'
import Header from '@/components/Layout/header/index'

export default {
  name: 'home',
  components :{
    Header
  },
  data () {
    return {
      dataList: []
    }
  },
  mounted () {
    this.getData()
  },
  methods: {
    getData () {
      let me = this
      api.addressList().then(res => {
        if (res.code === 200) {
          me.dataList = res.data.map(item => {
            item.visible = false
            return item
          })
        } else {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }
      }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
      }).finally(() => {
      })
    },
    updateDto (item) {
      let me = this
      let data = {
        defaultStatus: item.defaultStatus === 1 ? 0 : 1,
        id: item.id,
        memberId: item.memberId
      }
      api.updateDefaultStatus(data).then(res => {
        if (res.code === 200) {
          me.getData()
        } else {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }
      }).catch((res) => {
        me.$notify({
          message: res.message,
          color: '#ad0000',
          background: '#ffe1e1',
        })
      }).finally(() => {
      })
    },
    deleteDto (item) {
      let me = this
      me.$dialog.confirm({
        message: '您确认要删除该地址吗？',
      }).then(() => {
        api.addressDelete({id: item.id}).then(res => {
          if (res.code === 200) {
          } else {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }).finally(() => {
          me.getData()
        })
      }).catch(() => {
      });
     
    },
    addOrEdit (item) {
      this.$router.push({
        path: '/user/address/modify',
        query: {
          item: JSON.stringify(item)
        }
      })
    }
  }
}
</script>
<style lang="stylus" scoped>
@import '~@/styles/variable.styl';
.ar-body {
  min-height: 700px;
  background: #FAFAFA;
  .card-lists {
    background: #FFFFFF;
    margin: 10px;
    border-radius: 12px;
    border: 1px solid #FFFFFF;
    dl {
      margin: 18px;
      line-height: 22px;
      dt {
        margin-bottom: 15px;
        font-size: 16px;
        font-weight: bold;
        color: #222222;
        span {
          font-size: 16px;
          font-weight: bold;
          color: #222222;
          float: right;
        }
      }
      dd {
        color: #666666;
        border-bottom: 1px solid #EEEEEE;
        padding-bottom: 21px;
      }
      div {
        margin-top: 20px;
        img:first-child {
          width: 20px;
          vertical-align: top;
          margin-right: 10px;
        }
        >>>.van-icon {
          font-size: 20px;
          float: right;
          color: #FF4C4C!important;
        }
      }
    }
  }
  .btn {
    background: #ff4c4c;
    color: #FFFFFF;
    margin: 20px;
    height: 48px;
    line-height: 48px;
    text-align: center;
    font-size: 17px;
    border-radius: 4px;
  }
}

</style>



